import React, {forwardRef, useEffect, useState} from 'react';
import MaterialTable from 'material-table';
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import {firstLetterToLowerCase, numberWithSpaces} from "./settings/fns";
import axios from "axios";
import config from "./settings/config";
import Select from './UI/Select/Select';
import Button from "@material-ui/core/Button";

export default () => {
    const [state, setState] = useState({});
    const [providersDataPayOffs, setProvidersDataPayOffs] = useState({});
    const [total, setTotal] = useState(0);
    const [isLoad, setIsLoad] = useState(true);
    const [updateCement, setUpdateCement] = useState('');

    function sortBy(arr) {
        return arr.sort((a, b) => {
            return a[1] > b[1] ? 1 : -1;
        });
    }

    function sortByDate(arr) {
        return arr.sort((a, b) => {
            let t = a.period.split('.');
            let aDate = `${t[2]}-${t[1]}-${t[0]}`;
            t = b.period.split('.');
            let bDate = `${t[2]}-${t[1]}-${t[0]}`;
            return new Date(aDate) > new Date(bDate) ? 1 : -1;
        });
    }

    const summ = () => {
        let volume = document.getElementById('volume').value;
        let price = document.getElementById('pricePerOneTon').value;
        let element = document.querySelector('#subTotal');
        let parent = element.parentElement;
        let newElement = document.createElement('input');
        newElement.id = element.id;
        newElement.style = element.style.cssText;
        newElement.placeholder = element.placeholder;
        if (volume.includes(',')) {
            volume = volume.replace(',', '.').replace(' ', '')
        }
        if (price.includes(',')) {
            price = price.replace(',', '.').replace(' ', '')
        }
        newElement.value = Number(volume) * Number(price);
        document.querySelector('#subTotal').remove();
        parent.append(newElement);
    };

    const fetchDictionaryCement = async (t = null) => {
        const data = await axios.get(`${config.urlDB}dictionaryCement.json`);
        let dataTmpCement = {};
        let dataTmpCement2 = [];
        let dataTmpCement3 = [];
        let dataTmpCement4 = [];
        let providersDataPayOffTmp = [];
        let providersDataPayOffTemp = [];
        let subTotalTmp = 0;
        for (let item in data.data) {
            dataTmpCement2.push([item, data.data[item].name]);
            dataTmpCement3.push([item, data.data[item].name, data.data[item].price]);
        }

        let tmp = sortBy(dataTmpCement2);
        let tmp2 = sortBy(dataTmpCement3);

        tmp.map(item => {
            dataTmpCement[item[0]] = item[1];
        });

        tmp2.map(item => {
            dataTmpCement4[item[0]] = [item[1], item[2]];
        });

        const updatedCement = (cement) => {
            setUpdateCement(cement);
            let parent = document.querySelector('#pricePerOneTon').parentElement;
            let element = document.querySelector('#pricePerOneTon');
            let newElement = document.createElement('input');
            newElement.id = element.id;
            newElement.style = element.style.cssText;
            newElement.placeholder = element.placeholder;
            newElement.value = dataTmpCement4[cement][1];
            document.querySelector('#pricePerOneTon').remove();
            parent.append(newElement);
            summ();
        };

        const providersDataPayOff = await axios.get(`${config.urlDB}providersDataPayOff.json`);
        for (let item in providersDataPayOff.data) {
            if (localStorage.getItem('objects')) {
                JSON.parse(localStorage.getItem('objects')).map(value => {
                    if (JSON.parse(localStorage.getItem('currentProvider')) === providersDataPayOff.data[item].provider) {
                        if (value[0] === JSON.parse(localStorage.getItem('currentObject')) && value[1] === providersDataPayOff.data[item].object) {
                            if (!providersDataPayOff.data[item].comment.toLowerCase().includes('')) {
                                subTotalTmp += Number(providersDataPayOff.data[item].subTotal.replace(',', '.'));
                                if (providersDataPayOff.data[item].delivery) {
                                    subTotalTmp += Number(providersDataPayOff.data[item].delivery.replace(',', '.'));
                                }
                                if (providersDataPayOff.data[item].plain) {
                                    subTotalTmp += Number(providersDataPayOff.data[item].plain.replace(',', '.'));
                                }
                                if (providersDataPayOff.data[item].underloading) {
                                    subTotalTmp += Number(providersDataPayOff.data[item].underloading.replace(',', '.'));
                                }
                            }
                            providersDataPayOff.data[item].volume = (Number(providersDataPayOff.data[item].volume)  !== 'NaN' && Number(providersDataPayOff.data[item].volume) !== 0) ? numberWithSpaces(providersDataPayOff.data[item].volume.replace(',', '.')) : providersDataPayOff.data[item].volume;
                            providersDataPayOff.data[item].pricePerOneTon = (Number(providersDataPayOff.data[item].pricePerOneTon)  !== 'NaN' && Number(providersDataPayOff.data[item].pricePerOneTon) !== 0) ? numberWithSpaces(providersDataPayOff.data[item].pricePerOneTon.replace(',', '.')) : providersDataPayOff.data[item].pricePerOneTon;
                            providersDataPayOff.data[item].subTotal4Percent = (Number(providersDataPayOff.data[item].subTotal)  !== 'NaN' && Number(providersDataPayOff.data[item].subTotal) !== 0) ? numberWithSpaces(Number(providersDataPayOff.data[item].subTotal.replace(',', '.')) + providersDataPayOff.data[item].subTotal.replace(',', '.') / 100 * 4) : providersDataPayOff.data[item].subTotal;
                            providersDataPayOff.data[item].subTotal = (Number(providersDataPayOff.data[item].subTotal)  !== 'NaN' && Number(providersDataPayOff.data[item].subTotal) !== 0) ? numberWithSpaces(providersDataPayOff.data[item].subTotal.replace(',', '.')) : providersDataPayOff.data[item].subTotal;
                            providersDataPayOff.data[item].comment = firstLetterToLowerCase(providersDataPayOff.data[item].comment);
                            providersDataPayOff.data[item].isAccounting = providersDataPayOff.data[item].isAccounting ?? false;
                            providersDataPayOff.data[item].whoCompany = providersDataPayOff.data[item].whoCompany ?? '';
                            providersDataPayOffTmp.push({id: item, data: providersDataPayOff.data[item]});
                            providersDataPayOffTemp.push(providersDataPayOff.data[item]);
                        }
                    }
                });
            }
        }

        // sortByDate(providersDataPayOffTemp);

        setTotal(subTotalTmp);
        setProvidersDataPayOffs(providersDataPayOffTmp);

        setState({
            columns: [
                {
                    title: '  / ',
                    field: 'brandCement',
                    emptyValue: '',
                    editable: 'never',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    },
                    lookup: dataTmpCement,

                },
                {
                    title: '',
                    field: 'period',
                    emptyValue: '',
                    editable: 'never',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    }
                },
                {
                    title: '',
                    field: 'volume',
                    emptyValue: '',
                    editable: 'never',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    },
                },
                {
                    title: '',
                    field: 'subTotal',
                    emptyValue: '',
                    editable: 'never',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    },
                },
                {
                    title: ' (+4%)',
                    field: 'subTotal4Percent',
                    emptyValue: '',
                    editable: 'never',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    },
                },
                {
                    title: ' ',
                    field: 'isAccounting',
                    type: 'boolean',
                    emptyValue: false,
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    }
                },
                {
                    title: ' ',
                    field: 'whoCompany',
                    emptyValue: '',
                    headerStyle: {
                        textAlign: 'center'
                    },
                    cellStyle: {
                        fontSize: 'small',
                        padding: '0px',
                        textAlign: 'center'
                    },
                    render: (data) => {
                        return data.whoCompany === '' ? <span style={{color: '#ccc'}}> ...</span> : data.whoCompany;
                    }
                },
            ],
            data: providersDataPayOffTemp
        });
        setIsLoad(false);
    };

    useEffect(() => {
        fetchDictionaryCement();
    }, []);

    const tableIcons = {
        Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
        Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
        Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
        DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
        Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
        Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
        FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
        LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
        NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
        PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
        ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
        Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
        SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
        ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
        ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
    };

    return (
        <div style={{paddingBottom: 60}}>
            <MaterialTable
                icons={tableIcons}
                localization={{
                    header: {
                        actions: '',
                    },
                    body: {
                        emptyDataSourceMessage: '    ',
                        filterRow: {
                            filterTooltip: ''
                        },
                        addTooltip: '',
                        deleteTooltip: '',
                        editTooltip: '',
                        editRow: {
                            deleteText: '     ?',
                            cancelTooltip: '',
                            saveTooltip: ''
                        }
                    },
                    grouping: {
                        placeholder: ' ...'
                    },
                    toolbar: {
                        addRemoveColumns: '   ',
                        nRowsSelected: '{0} () ',
                        showColumnsTitle: ' ',
                        showColumnsAriaLabel: ' ',
                        exportTitle: '',
                        exportAriaLabel: '',
                        exportName: '   CSV',
                        searchTooltip: '',
                        searchPlaceholder: '',
                    },
                    pagination: {
                        labelDisplayedRows: '{from}-{to}  {count}',
                        labelRowsSelect: '',
                        labelRowsPerPage: '  :',
                        firstAriaLabel: ' ',
                        firstTooltip: ' ',
                        previousAriaLabel: ' ',
                        previousTooltip: ' ',
                        nextAriaLabel: ' ',
                        nextTooltip: ' ',
                        lastAriaLabel: ' ',
                        lastTooltip: ' ',
                    },
                }}
                title={JSON.parse(localStorage.getItem('currentObject'))}
                columns={state.columns}
                data={state.data}
                options={{
                    sorting: false,
                    selection: false,
                    grouping: false,
                    filtering: false,
                    exportButton: true,
                    pageSizeOptions: [10],
                    pageSize: 10,
                    draggable: false,
                    exportAllData: true,
                    paging: false
                }}
                detailPanel={rowData => {
                    if (JSON.parse(localStorage.getItem('currentProvider')) === '-M3ytpeFI7Cry9gqb6c9') {
                        return (
                            <div style={{width: '400px', margin: '10px auto'}}>
                                <span>
                                    <label>: </label>
                                    <label
                                        style={{border: 'none', borderBottom: '1px solid #ccc', outline: 'none', marginTop: '5px', paddingBottom: '6px', width: '150px', marginRight: '30px'}} id="delivery"
                                    >
                                        {rowData.delivery ? numberWithSpaces(rowData.delivery) : 0}
                                    </label>
                                </span>
                                <span>
                                    <label>: </label>
                                    <label
                                        style={{border: 'none', borderBottom: '1px solid #ccc', outline: 'none', marginTop: '5px', paddingBottom: '6px', width: '150px', marginRight: '30px'}} id="underloading"
                                    >
                                        {rowData.underloading ? numberWithSpaces(rowData.underloading) : 0}
                                    </label>
                                </span>
                                <span>
                                    <label>: </label>
                                    <label
                                        style={{border: 'none', borderBottom: '1px solid #ccc', outline: 'none', marginTop: '5px', paddingBottom: '6px', width: '150px',}} id="plain"
                                    >
                                        {rowData.plain ? numberWithSpaces(rowData.plain) : 0}
                                    </label>
                                </span>
                            </div>
                        )
                    }
                }}
                onRowClick={(event, rowData, togglePanel) => {
                    if (JSON.parse(localStorage.getItem('currentProvider')) === '-M3ytpeFI7Cry9gqb6c9') {
                        togglePanel();
                    }
                }}
                isLoading={isLoad}
                editable={{
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                if (oldData) {
                                    setState((prevState) => {
                                        const data = [...prevState.data];
                                        providersDataPayOffs.map(item => {
                                            if (item.data === data[data.indexOf(oldData)]) {
                                                axios.patch(`${config.urlDB}providersDataPayOff/${item.id}.json`, {isAccounting: newData['isAccounting'], whoCompany: newData['whoCompany']}).then(res => {
                                                    fetchDictionaryCement();
                                                });
                                            }
                                        });
                                        data[data.indexOf(oldData)] = newData;
                                        return { ...prevState, data };
                                    });
                                }
                            }, 600);
                        }),
                }}
            />
            <p>: {numberWithSpaces(total)}</p>
        </div>
    );
}
